                >li{
                    display:flex;
                    flex-direction:column;
                    margin-bottom:0.32rem;
                    margin-right:0.32rem;
                    >img{
                        width:2.6rem;
                        height:3.2rem;
                    }
                    >span{
                        margin-top:0.27rem;
                        color:rgba(255, 255, 255, 0.8);
                        font-size:0.32rem;
                    }
                }
                >.SpecialTopicBody_barColor{
                    border:0.07rem solid #FF5E4D;
                }
            }
        }
        >.AssistTicket_btn{
            width:3.253rem;
