            border-radius:2.67rem;
            text-align :center;
            line-height :0.853rem;
            margin-top:0.48rem;
            margin-bottom:0.48rem;
        }
    }
    //
    >.FreeTicket{
        display :flex;
        justify-content:space-between;
        align-items :center;
        margin:0.53rem 0.4rem;
        >.FreeTicket_title{
            font-size:0.427rem;
            color:rgba(255, 255, 255, 1);
            line-height :0.613rem;
        }
        >.FreeTicket_btn{
            width:2.4rem;
            height:0.64rem;
            background:rgba(255, 157, 172, 1);
            font-size:0.347rem;
            color:rgba(255, 255, 255, 1);
            border-radius:2.67rem;
            text-align :center;
            line-height :0.64rem;
        }
    }
}
